body {
  background-color: #000000;
  font-family: 'Poppins', sans-serif;
}

#app {
  background-color: #3c424a;
  height: 100vh;
}

.header {
  color: #ffffff;
}

.header h1 {
  border-bottom: solid;
  border-color: #e65c8b;
}

.tasksContainer {
  max-height: 420px;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  display:none; 
}

.zeroTasks {
  background-color: #343a40;
  color: #ffffff;
  border-radius: 10px;
}

.task {
  background-color: #343a40;
  color: #ffffff;
  border-radius: 10px;
  font-weight: 300;
}

.pTask {
  overflow-x: scroll;
}

.btn-close {
  box-sizing: initial;
}

.addTaskBtn {
  position: fixed;
  bottom: 30px;
  left: 0;
}

.addTaskBtn button {
  font-size: 20px;
  color: #ffffff;
  width: 50px;
  height: 50px;
  border-radius: 100%;
}

#sidebar {
  width: 320px;
  height: 100vh;
  position: fixed;
  top: 0%;
  right: 0;
  background-color: #e65c8b;
}

.sidebarTitle {
  color: #ffffff;
}

#taskBox {
  height: 60px;
  border-radius: 10px;
  outline: none;
  font-weight: 300;
}